/* App.css */

body {
    font-family: 'Arial', sans-serif;
    background-color: #f0f8ff;
    color: #333;
    margin: 0;
    padding: 0;
  }
  
  .landing-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    text-align: center;
  }
  
  .header {
    margin-bottom: 30px;
  }
  
  .title {
    font-size: 3rem;
    color: #006994;
  }
  
  .subtitle {
    font-size: 1.5rem;
    color: #007bb5;
  }
  
  .content {
    margin-top: 20px;
    background-color: #fff;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 600px;
  }
  
  .description {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 30px;
  }
  
  .cta-button {
    background-color: #007bb5;
    color: white;
    padding: 15px 30px;
    text-decoration: none;
    font-size: 1.2rem;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .cta-button:hover {
    background-color: #005f7f;
  }
  
  .footer {
    position: absolute;
    bottom: 10px;
    font-size: 1rem;
    color: #888;
  }
  